<template>
	<view class="list_goods">
		<view class="row">
			<view v-for="(o, i) in list" :key="i" :class="'col-' + col" class="col">
				<navigator :url="'/pages/goods/details?goods_id=' + o[vm.goods_id]" class="goods">
					<image style="width: 100%; height: auto;" :src="$fullUrl(o[vm.img])" mode="widthFix"></image>
					<view class="good_bottom">
						<view class="title_wrap"><text class="ellipsis_2">{{ o[vm.title] }}</text></view>
						<view style="position: relative;">
							<text style="font-size: 0.5rem;color:var(--color_grey)"></text><text class="price">￥{{ o[vm.price] }}</text><text
							 class="price_ago">￥{{ o[vm.price_ago] }}</text>
							 <uni-icons class="icon_cart" type="cart" size="14"></uni-icons>
						</view>
					</view>
				</navigator>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			url: {
				type: String,
				default: '/pages/goods/details?goods_id='
			},
			list: {
				type: Array,
				default: function() {
					return [];
				}
			},
			vm: {
				type: Object,
				default: function() {
					return {
						goods_id: "goods_id",
						img: "img",
						title: "title",
						price: "price",
						price_ago: "price_ago"
					}
				}
			},
			span: {
				type: Number,
				default: 2
			}
		},
		data() {
			var col = 12 / this.span;
			return {
				col
			}
		}
	}
</script>

<style scoped>
	.ellipsis_2 {
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
	}

	.list_goods {
		background-color: #f8f8f8;
	}

	.goods {
		width: 95%;
		margin: 0 auto;
		margin-top: 0.5rem;
		border: 1rpx solid #eee;
		border-radius: 5rpx;
		background-color: #fff;
	}

	.good_bottom {
		padding: 0.2rem 0.5rem;
		padding-bottom: 0.5rem;
		font-size: 0.8rem;

	}

	.title_wrap {
		margin-bottom: 0.3rem;
	}

	.price {
		color: var(--color_primary_b);
		font-weight: 600;
	}

	.price_ago {
		margin-left: .3rem;
		text-decoration: line-through;
		font-size: 0.4rem;
		color: #999;
	}
	.icon_cart{
		position: absolute;
		right: 0;
	}
</style>
